body {
  box-sizing: border-box;
  font-family: Arial;

  
}

.people {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, 200px);
  gap: 40px 30px;
  padding: 30px 35px 0;


  &__person {
    
    display: block;
    justify-items: center;
    width: 200px;
    height: 231px;
    padding: 15px;
    
    &-photo {
      position: relative;
      justify-content: center;
      
      width: 200px;
      height: 200px;

      padding-bottom: 8px;

      img {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        object-fit: cover;
        transition: 1s;

      }

      &-rank {
        position: absolute;
        bottom: 84px;
        right: 10px;
        
        display: flex;
        align-items: center;
        justify-content: center;
        
        width: 32px;
        height: 32px;

        font-weight: 700;
        font-size: 16px;
        line-height: 100%;
        text-align: center;


        background-color: white;
        color: #8B0000;
        border-radius: 50%;
      }
    }

    &-details {
      display: block;
      justify-items: center;

      &-name {
        font-weight: 700;
        font-size: 20px;
        line-height: 100%;
        text-align: center;
        color: #2F4F4F;
      }

      &-extra {
        display: none;

        padding-top: 9px;
        font-family: Arial;
        font-weight: 400;
        font-size: 18px;
        line-height: 100%;
        text-align: center;
        color: #000000;

      }
    }

    &:hover {
      border-radius: 10px;
      background-color: #EEEEEE;
      scale: 1.2;
      z-index: 1;
      
      .people__person-photo {
        img {
          transition: 1s;
          border-radius: 30%;
        }
      }

       
      .people__person-details-extra {
          display: block;
        
      }
    }
  }

  
}

@media (min-width: 751px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}
